/*-----Testimonial slide-----------*/

.Testimonial .slide {
    max-width: 80%;
    margin: auto;
    height: 500px;
    position: relative;
    display: flex;
    overflow: hidden;
}

article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vh;
    height: 100%;
    opacity: 0;
    transition: all 0.3s linear;
}

article.activeSlide {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

article.lastSlide {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

article.nextSlide {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}


/*-----Testimonial slide-----------*/

@media (max-width: 768px) {
    article {
        flex-direction: column;
        padding: 20px;
    }
    .Testimonial .left {
        width: 100%;
        display: flex;
        margin: 0;
        margin-top: 15%;
    }
    .Testimonial .left img {
        /* width: 190px; */
        border-radius: 10px;
        margin-right: 20px;
        border-radius: 15px;
        position: relative;
        bottom: 50px;
    }
    .Testimonial .right {
        width: 100%;
        margin: 0;
        padding: 0 0 0 0px;
    }
    .Testimonial .slide {
        height: 100vh;
        max-width: 90%;
    }
    .Testimonial .quote {
        display: none;
    }
}
